.bounceball {
  position: relative;
  display: inline-block;
  height: 37px;
  width: 15px;
  
}
.bounceball:before {
    position: absolute;
    content: "";
    display: block;
    top: 0;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #fbae17;
    -webkit-transform-origin: 50%;
            transform-origin: 50%;
    -webkit-animation: bounce 500ms alternate infinite ease;
            animation: bounce 500ms alternate infinite ease;
  }
  @-webkit-keyframes bounce {
    0% {
      top: 30px;
      height: 5px;
      border-radius: 60px 60px 20px 20px;
      -webkit-transform: scaleX(2);
              transform: scaleX(2);
    }
    35% {
      height: 15px;
      border-radius: 50%;
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
    }
    100% {
      top: 0;
    }
  }
  
  @keyframes bounce {
    0% {
      top: 30px;
      height: 5px;
      border-radius: 60px 60px 20px 20px;
      -webkit-transform: scaleX(2);
              transform: scaleX(2);
    }
    35% {
      height: 15px;
      border-radius: 50%;
      -webkit-transform: scaleX(1);
              transform: scaleX(1);
    }
    100% {
      top: 0;
    }
  }


  .lds-facebook {
    display: inline-flex;
    position: relative;
    width: 20px;
    height: 0px;
  }
  .lds-facebook div {
    display: inline-block;
    position: absolute;
    width: 3px;
    background: #fff;
    -webkit-animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
            animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
  }
  .lds-facebook div:nth-child(1) {
    left: 8px;
    -webkit-animation-delay: -0.24s;
            animation-delay: -0.24s;
  }
  .lds-facebook div:nth-child(2) {
    left: 14px;
    -webkit-animation-delay: -0.12s;
            animation-delay: -0.12s;
  }
  .lds-facebook div:nth-child(3) {
    left: 20px;
    -webkit-animation-delay: 0;
            animation-delay: 0;
  }
  @-webkit-keyframes lds-facebook {
    0% {
      top: -5px;
      height: 16px;
    }
    50%, 100% {
      top: -3px;
      height: 8px;
    }
  }
  @keyframes lds-facebook {
    0% {
      top: -5px;
      height: 16px;
    }
    50%, 100% {
      top: -3px;
      height: 8px;
    }
  }
  

  
.popup {
    position:fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 1200vh;
    background-color:rgba(36, 43, 49, 0.2);
    display:-ms-grid;
    justify-content: center;
    align-items: center;
    overflow-y:scroll;
    max-height:100%;
}

.popup-inner {
    position: relative;
    padding:32px;
    width: 100%;
    max-width: 800px;
    background-attachment: fixed;
    background-color: #363e46;
    background-image: linear-gradient(140deg, #485461 0%, #28313b 74%);
    overflow-y:scroll; 
    max-height:100%;
    border-width:1px;  
    border-style:solid;
    border-color: #839ed1;

}

.popup-inner .close-btn {
    position: absolute;
    top: 100px;
    right: 25px;
}

.popup-inner .close {
    position: absolute;
    top: 80px;
    right: 25px;
    background-color: #00325a;
}


